import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Vuex from 'vuex'
import './registerServiceWorker'
import ArgonDashboard from './plugins/argon-dashboard'
import store from './store'
import axios from 'axios'
import Vuelidate from 'vuelidate'
import Notifications from 'vue-notification'
import VueSweetalert2 from 'vue-sweetalert2';
import Fingerprint2 from 'fingerprintjs2'

import handleNotifications from "./utils/handleNotifications";
import helpers from "./utils/helpers";
import CripLoading from "crip-vue-loading"
import 'sweetalert2/dist/sweetalert2.min.css';
import dotend from 'dotenv';

dotend.config();
Vue.config.productionTip = false;

Vue.use(ArgonDashboard);
Vue.use(Vuex);
Vue.use(Vuelidate);
Vue.use(Notifications);
Vue.use(VueSweetalert2);
Vue.use(CripLoading, {
  axios: axios,
  color: "#204d74",
  failColor: "#ff1200",
});


Vue.component('pagination', require('laravel-vue-pagination'));

// set token in axios header if page refreshed
const token = localStorage.getItem('admin-token');
if (token) {
  axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
}

window.EventBus = new Vue();
handleNotifications(this);

new Vue({
  router,
  render: h => h(App),
  store,
  mixins: [helpers],
  methods: {
    initiateApplication() {

      Fingerprint2.get(function (components) {
        // window.console.log(components) // an array of components: {key: ..., value: ...}
        // window.console.log(x);

        let values = components.map(function (component) { return component.value })
        axios.defaults.headers.common['X-APP-FP'] = Fingerprint2.x64hash128(values.join(''), 31);
      });

      if(process.env.VUE_APP_APP_NAME) {
        axios.defaults.headers.common['X-APP-NAME'] = process.env.VUE_APP_APP_NAME;
      }
      if(process.env.VUE_APP_APP_VERSION) {
        axios.defaults.headers.common['X-APP-VERSION'] = process.env.VUE_APP_APP_VERSION;
      }
    }
  },
  created() {
    this.initiateApplication();
  }
}).$mount('#app');