import { USER_REQUEST, USER_ERROR, USER_SUCCESS } from "../actions/user";
import apiCall from "../../utils/api";
import Vue from "vue";
import { AUTH_LOGOUT } from "../actions/auth";
import URL from "../../utils/url";

const state = { status: "", profile: {}, permissions: [] };

const getters = {
  getProfile: state => state.profile,
  getPermissions: state => state.permissions,
  isProfileLoaded: state => !!state.profile.name
};

const actions = {
  [USER_REQUEST]: ({ commit, dispatch }) => {
    commit(USER_REQUEST);
    apiCall({ url: URL.baseURL + URL.userInfoURL, method : 'GET' })
      .then(resp => {
        commit(USER_SUCCESS, resp);
      })
      .catch(() => {
        commit(USER_ERROR);
        // if resp is unauthorized, logout, to
        dispatch(AUTH_LOGOUT);
      });
  }
};

const mutations = {
  [USER_REQUEST]: state => {
    state.status = "loading";
  },
  [USER_SUCCESS]: (state, resp) => {
    state.status = "success";
    // state.profile = resp.data;
    let permissions = [];
    // let roles = resp.data.roles !== undefined ? resp.data.roles.map(x => x.permissions).flat(1) : null;
    // if(roles != null) {
    //   permissions = roles.map(x => x.name );
    // }

    permissions = resp.data.permissions

    Vue.set(state, "profile", resp.data);
    Vue.set(state, "permissions", permissions);
  },
  [USER_ERROR]: state => {
    state.status = "error";
  },
  [AUTH_LOGOUT]: state => {
    state.profile = {};
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
