import Vue from 'vue';

const handleNotifications = (context) => {
    window.EventBus.$on('deleted', function(data){
        Vue.notify({type: 'danger', msg_text: data.message});
        context.msg_text = data.message;
    });
    window.EventBus.$on('expired', function(data){
        sendNotification(context, "Expired", data.message, 'warn');
    });
    window.EventBus.$on('success', function(data){
        sendNotification(context, "Success", data.message, 'success');
    });
    window.EventBus.$on('error', function(data){
        sendNotification(context, "Oops", data.message, 'danger');
    });
};
const sendNotification = (context, title, message, type) => {
    // Vue.notify({
    //     group: 'notification',
    //     title: title,
    //     text: message,
    //     type: type
    // });

    if(type === 'warn') { type = 'warning'; }
    if(type === 'danger') { type = 'error'; }
    Vue.swal({
        title: title,
        icon: type,
        text: message,
        timer: 4000,
        timerProgressBar: true,
        width: '300px',
        toast: true,
        position: 'top-end',
        showConfirmButton: false
    })
};
export default handleNotifications;