<template>
    <base-nav class="navbar-top navbar-dark"
              id="navbar-main"
              :show-toggle-button="false"
              expand>
        <form class="navbar-search navbar-search-dark form-inline mr-3 d-none d-md-flex ml-lg-auto">
            <div class="form-group mb-0 d-none">
                <base-input placeholder="Search"
                            class="input-group-alternative"
                            alternative=""
                            addon-right-icon="fas fa-search">
                </base-input>
            </div>
        </form>
        <ul class="navbar-nav align-items-center d-none d-md-flex">
            <li class="nav-item dropdown">
                <base-dropdown class="nav-link pr-0">
                    <div class="media align-items-center" slot="title">
                <span class="avatar avatar-sm rounded-circle">
                  <img alt="Image placeholder" src="img/theme/user-male.png">
                </span>
                        <div class="media-body ml-2 d-none d-lg-block">
                            <span class="mb-0 text-sm  font-weight-bold">{{ profile.name }}</span>
                        </div>
                    </div>

                    <template>
                        <div class=" dropdown-header noti-title">
                            <h6 class="text-overflow m-0">Welcome!</h6>
                        </div>
                        <router-link to="/profile" class="dropdown-item">
                            <i class="ni ni-single-02"></i>
                            <span>My profile</span>
                        </router-link>
                        <!-- <router-link to="/profile" class="dropdown-item">
                            <i class="ni ni-calendar-grid-58"></i>
                            <span>Activity</span>
                        </router-link> -->
                        <div class="dropdown-divider"></div>
                        <!--<router-link to="#!" class="dropdown-item" v-on:click="logout">
                            <i class="ni ni-user-run"></i>
                            <span>Logout</span>
                        </router-link>-->
                        <a href="#!" class="dropdown-item" v-on:click="logout">
                            <i class="ni ni-user-run"></i>
                            <span>Logout</span>
                        </a>
                    </template>
                </base-dropdown>
            </li>
        </ul>
    </base-nav>
</template>
<script>
    import { AUTH_LOGOUT} from "../store/actions/auth";

    export default {
        name: 'dashboard-navbar',
        props: {
            profile: Object
        },
        data() {
            return {
                activeNotifications: false,
                showMenu: false,
                searchQuery: '',
            };
        },
        methods: {
            toggleSidebar() {
                this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
            },
            hideSidebar() {
                this.$sidebar.displaySidebar(false);
            },
            toggleMenu() {
                this.showMenu = !this.showMenu;
            },
            logout: function () {
                this.$store.dispatch(AUTH_LOGOUT)
                    .then(() => {
                        this.$router.push('/login', () => {
                            window.EventBus.$emit('success', {
                                message: 'See you soon, logged out successfully',
                            });
                        })
                    }, () => {
                        // If any error generated, then manually do the logout as user wants to logout
                        localStorage.removeItem("admin-token");
                        sessionStorage.clear();

                        this.$router.push('/login', () => {
                            window.EventBus.$emit('success', {
                                message: 'See you soon, logged out successfully',
                            });
                        })
                    })
            }
        }
    };
</script>
