import store from '../store';

export default {
    methods: {
        $can(permissionName, isMenu = false) {
            if (isMenu) {
                return store.state.user.permissions.some(s => s.indexOf(permissionName) >= 0);
            } else {
                return store.state.user.permissions.indexOf(permissionName) !== -1;
            }
        },
        $showError(error) {
            if (error.response) {
                if (error.response.data) {
                    if (typeof (error.response.data) == 'string') {
                        return error.response.data;
                    } else if (typeof (error.response.data) == 'object') {
                        if (error.response.data.errors && typeof (error.response.data.errors) == 'object') {
                            let resp = "";
                            Object.keys(error.response.data.errors).forEach((key) => {
                                resp += error.response.data.errors[key][0] + (resp === '' ? '' : ', ');
                            });
                            return resp;
                        } else if (error.response.data.message) {
                            return error.response.data.message;
                        } else {
                            return error.response.data;
                        }
                    } else {
                        return error.response.data;
                    }
                } else {
                    return error;
                }
            } else if (error.message) {
                return error.message;
            } else {
                return error;
            }

            /*if (typeof (error) == 'string') {
                return error;
            } else if (typeof (error) == 'object') {

                if (typeof (error.response.data.message) !== 'undefined') {
                    return error.response.data.message;
                } else if (typeof (error.message) !== 'undefined') {
                    return error.message;
                } else if (typeof (error.error) !== 'undefined') {
                    return error.error[0];
                } else {
                    return error;
                }
            } else {
                return error;
            }*/
        }
    }
};