<style>
@media screen and (max-width: 768px) {
    .width-50-mobile {
        width : 50% !important;
    } 
}
</style>
<template>
    <div id="app">
        <notifications group="notification" />
        <notifications></notifications>
        <router-view/>
    </div>
</template>
<script>
    import axios from 'axios';
    import {AUTH_SINGLE_LOGOUT} from "./store/actions/auth";
    import store from './store';
    import router from './router'

    export default {
        created: function () {

            let axiosResponseInterceptor = axios.interceptors.response.use(
                response => {
                    return response;
                },
                error => {
                    window.console.log(error.message);
                    // let errorResponse = error.response;
                    if (error && error.response && error.response.status && error.response.status === 401) {
                        axios.interceptors.response.eject(axiosResponseInterceptor);
                        store.dispatch(AUTH_SINGLE_LOGOUT).then(() => {
                            router.push('/login', () => {
                                window.EventBus.$emit('expired', {
                                    message: 'Your session was expired, please login again',
                                });
                            })
                        });
                    }
                    return Promise.reject(error);
                }
            );
        }
    }
</script>